import { Component } from '@angular/core';
import { ActionSheetController  ,AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { Socket } from 'ng-socket-io';

@IonicPage()
@Component({
  selector: 'page-lluvia-ideas',
  templateUrl: 'lluvia-ideas.html',
})
export class LluviaIdeasPage {
  idsuscriptor: any;
  fotott: any;
  nombrett: any;
  username: string = "";
  nidsuscriptor: any;
  tiposus: any=[];
  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  permisom: any=[];
    generated = '';
    public hasFilter: boolean = false;
   idempresa:any;
   
   idafiliado:any='na';
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';

//private barcodeScanner:BarcodeScanner,
toUser : {toUserId: string, toUserName: string};
  constructor(private socket: Socket,public actionSheetCtrl: ActionSheetController,
    public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider,
     public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth
    //, private qrScanner: QRScanner
   
    ) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })

    
  }

  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  showFilter()     
  {

    let self = this;
    self.loading = true;
   
  let actionSheet = this.actionSheetCtrl.create({
    title: 'Filtrar por estado:',
    buttons: [
      {
        text: 'Activos',
        handler: () => {
        
          this.items=this.items2
          this.items= this.items2.filter((v) => {
         
            if(v.estado ) {
              
              if (v.estado=='Activo') {
                return true;
              }
              return false;
              
            }
          });
          self.loading = false;
         
          this.hasFilter = true;
        }
      },
      {
        text: 'Inactivos',
        handler: () => {
          this.items=this.items2
          this.items= this.items2.filter((v) => {
            if(v.estado ) {
              self.loading = false;
              if (v.estado=='Inactivo') {
                return true;
              }
              return false;
              
            }
          });   
        }
      }
     
    ]
  });

  actionSheet.present();


   
  }

 

  
  OnExport = function ()
  {
    this.regService.abrelinkinterno('datosfijos/excel-plantillas');
    
  }



  pideregistros() { 

    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value

      let self = this;
      self.loading = true;
      this.regService.getReg(this.idempresa +'/5cdec5279312042f589bf0b8/categoria','mailmarkets').then((data) => {
           
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });
    });
      
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {
   
    
      
  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
  this.idsuscriptor=this.tiposus[3]
this.nombrett=this.tiposus[4]   
this.fotott=this.tiposus[9]+","+this.tiposus[10]

this.pideregistros();
  });



  
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'plantillas').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


    
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('PlantillaCreatePage', { record : item,idempresa:this.idempresa },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros() 
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('PlantillaCreatePage',{ record : null ,idempresa:this.idempresa},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros() 
      }  
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }
   participaRecord(item : any) : void
   {
   
      this.navCtrl.push('PlantilladListPage', { record : item,record2:{idempresa:this.idempresa,idplantilla:item._id,nombrep:item.nombre} });
   }
   conferenciaRecord(item : any) : void
   {
   
      this.navCtrl.push('PlantilladmListPage', { record : item,record2:{idempresa:this.idempresa,idplantilla:item._id,nombrep:item.nombre} });
   }

   viewRecord(item : any): void
   {
     // let modal = this._MODAL.create(ViewGalleryPage, { record : item });
     // modal.present();
   }


   
  tomaqrpage(item : any) : void
  {
   let addModal = this._MODAL .create('CodebarpagPage', { record : item },{showBackdrop: true,enableBackdropDismiss: false});
   
   addModal.present();
   //  this.navCtrl.push('EmpresaCreatePage', { record : item });
  }
 
    
  verinfo(item,op){
    this.navCtrl.push('CursosviewHtmlPage', { html: item.texto,record : item,titulo:item.nombre});

      

 ;


    


  }

  iniciarcurso(item){    
   
   


    this.regService.getReg(item._id+"@gmail.com/emailsolo/" + this.idempresa,'personals').then((data) => {
      this.socket.connect();
    this.socket.emit('set-nickname', data[0]._id);
    this.navCtrl.push('ChatPage', { record :{  toUserId:this.idsuscriptor,
      toUserName:this.nombrett,avatar:this.fotott} ,grupal:'si',
      record2:{  toUserId:data[0]._id,
    toUserName: 'Aporta al tema',avatar:data[0].foto}});
    });
    
  }


}



